import React, { useState, useEffect } from "react";
import { POLL_STATES } from "../../Constants/PollStates";

export function PollResult(props) {
  const { index: currentIndex } = props;
  const { question, options, index, totalResponse } = props.data;
  options.sort(function (a, b) {
    return a.id - b.id;
  });

  return (
    <div className="pollBox__question">
      <h3 className="pollBox__title">{`Q${currentIndex + 1}. ${question}`}</h3>
      <ul className="pollBox__options">
        {options.map((option) => (
          <li>
            <div className="custom-slider">
              <span className="custom-slider__text">{option.value}</span>
              <div className="custom-slider__bar">
                <span className="custom-slider__mark">{`${
                  totalResponse > 0
                    ? (option.response / totalResponse) * 100
                    : 0
                }%`}</span>
                <div
                  className="custom-slider__bar-inner"
                  style={{
                    width: `${
                      totalResponse > 0
                        ? (option.response / totalResponse) * 100
                        : 0
                    }%`,
                  }}
                ></div>
              </div>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}

export function PollQuestion(props) {
  const { handleSubmit, checkIfAlreadyAnswered, index: currentIndex } = props;
  const { question, options, feedbacks, index, id, eventId } = props.data;
  options?.sort(function (a, b) {
    return a.id - b.id;
  });

  const [answer, setAnswer] = useState(checkIfAlreadyAnswered);
  const [showError, setShowError] = useState(false);
  const [feedbackSuccess, setFeedbackSuccess] = useState(false);
  const [feedback, setFeedback] = useState("");

  useEffect(() => {
    if (checkIfAlreadyAnswered) {
      setAnswer(checkIfAlreadyAnswered);
    }
  }, [checkIfAlreadyAnswered]);

  const handleSubmitButton = (e) => {
    e.preventDefault();
    if (!answer) {
      setShowError(true);
      return;
    }
    setShowError(false);
    handleSubmit(id, answer, "multiple");
  };

  const handleFeedbackSubmitButton = (e) => {
    e.preventDefault();
    if (!feedback) {
      setShowError(true);
      return;
    }
    setShowError(false);
    handleSubmit(id, feedback, "feedback");
    setFeedbackSuccess(true);
    setFeedback("");
  };

  return (
    <div className="pollBox__question">
      {options && (
        <form onSubmit={handleSubmitButton}>
          <h3 className="pollBox__title">{`Q${
            currentIndex + 1
          }. ${question}`}</h3>
          <ul className="pollBox__options">
            {options.map((option, index) => (
              <li key={`option-${option.id}`}>
                <label
                  key={`checkbox-${option.id}`}
                  className="custom-checkbox"
                >
                  {`0${index + 1}.  ${option.value}`}
                  <input
                    type="radio"
                    name={option.id}
                    checked={!answer ? false : option.id === answer.id}
                    onChange={(e) => {
                      setShowError(false);
                      setAnswer(option);
                    }}
                    disabled={checkIfAlreadyAnswered === null ? false : true}
                  />
                  <span className="custom-checkbox__icon icon-unchecked2"></span>
                </label>
              </li>
            ))}
          </ul>
          {showError && (
            <>
              <div style={{ color: "red", marginBottom: "0rem" }}>
                * Please give some response first, to submit.
              </div>
              <br></br>
            </>
          )}
          <div style={{ textAlign: "center" }}>
            <button
              type="submit"
              className="btn btn-secondary"
              disabled={checkIfAlreadyAnswered === null ? false : true}
            >{`${
              checkIfAlreadyAnswered === null ? "Submit" : "Answered"
            }`}</button>
          </div>
        </form>
      )}

      {feedbacks && (
        <form onSubmit={handleFeedbackSubmitButton}>
          {!feedbackSuccess ? (
            <div>
              <h3 className="pollBox__title">{`Q${
                currentIndex + 1
              }. ${question}`}</h3>
              <ul className="pollBox__options">
                <input
                  type="text"
                  placeholder="Write text here..."
                  value={feedback}
                  onChange={(e) => {
                    setShowError(false);
                    setFeedback(e.target.value);
                  }}
                />
              </ul>
              {showError && (
                <>
                  <div style={{ color: "red", marginBottom: "0rem" }}>
                    * Please give some response first, to submit.
                  </div>
                  <br></br>
                </>
              )}
              <div style={{ textAlign: "center" }}>
                <button type="submit" className="btn btn-secondary">
                  Submit
                </button>
              </div>
            </div>
          ) : (
            <div style={{ textAlign: "center", position: "relative" }}>
              <div className="success-positioning d-flex">
                <div className="success-icon">
                  <div className="success-icon__tip"></div>
                  <div className="success-icon__long"></div>
                </div>
              </div>
              <h4>Feedback Submit Successfully</h4>
              <div
                onClick={() => {
                  setFeedbackSuccess(!feedbackSuccess);
                }}
                className="feedback-modal-cls-btn"
              >
                <img
                  src={process.env.PUBLIC_URL + "assets/images/icons/close.svg"}
                  alt=""
                  height="25px"
                />
              </div>
            </div>
          )}
        </form>
      )}
    </div>
  );
}

export function PollUser(props) {
  const { visiblePollData, submitResponse, pollAnswerredData } = props;
  return (
    <div className="sidebar__body">
      <div className="">
        {/* <div className="pollBox__header">
          <div className="d-flex justify-content-between align-items-center pd-b10">
            <h2 className="pollBox__title">Ask a question</h2>
            <button className="btn btn-grey pollBox__close">Close</button>
          </div>
          <div className="pollBox__send mg-b20">
            <img src="assets/images/user.png" alt="" />
            <div className="form-group">
              <input
                type="text"
                className="form-control"
                placeholder="Ask a question"
              />
              <button className="btn">
                <i className="icon-send"></i>
              </button>
            </div>
          </div>
          <h2 className="pollBox__title mg-b10">Polls</h2>
        </div> */}
        <div className="pollBox__body">
          {visiblePollData && visiblePollData.length === 0 && (
            <div
              className="noVisiblePoll"
              style={{
                textAlign: "center",
                marginTop: "1rem",
              }}
            >
              {/* <h2>Answer polls and quizes</h2> */}
              <p>
                As soon as a poll is posted <br></br>you will be able to express
                your opinion.
              </p>
            </div>
          )}
          {visiblePollData &&
            visiblePollData.map((pollItem, index) => (
              <div
                className="pollBox"
                key={pollItem.id}
                style={{ overflow: "hidden" }}
              >
                {pollItem.state === POLL_STATES.showQuestion && (
                  <PollQuestion
                    data={pollItem}
                    handleSubmit={submitResponse}
                    checkIfAlreadyAnswered={pollAnswerredData[pollItem.id]}
                    index={index}
                  />
                )}
                {pollItem.state === POLL_STATES.showResult && (
                  <PollResult data={pollItem} index={index} />
                )}
              </div>
            ))}
        </div>
      </div>
    </div>
  );
}
