import {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { ChatContext } from "../../Context/Chat/ChatContextProvider";
import { AvatarStyle, getInitals, LOREM_TEXT } from "../../Utility";
import { ReceivedMsg, SentMsg } from "../Messages";
import "../../Assets/css/chatRoom.css";
import {
  attachNewMessagesListener,
  attachOnDisconnectForTyping,
  attachTypingIndicator,
  removeNotification,
  sendMessage1on1,
  singalTypingIndicator,
} from "../../Firebase/chatManager";
import { UserContext } from "../../Context/Auth/UserContextProvider";
import { firestore, database, fieldValue } from "../../Firebase";
import { AppString } from "../../Firebase/constant";
import arrowSvg from "../../Assets/svg/arrow.svg";
import { UIContext } from "../../Context/UIContextProvider";
import { RiMailSendLine } from "react-icons/ri";
import swal from "sweetalert";

const email_server_endpoint =
  "https://mailsender-dot-vc-copy.el.r.appspot.com/sendChatMail/"; // "http://192.168.0.35:8080/sendChatMail/";

export default function PrivateChatRoom() {
  const { user } = useContext(UserContext);
  const { activeRoom, closePreviousChatRoom, allParticipantList } =
    useContext(ChatContext);
  const { getParticipantColorNumber } = useContext(UIContext);

  const [typedMessage, setTypedMessage] = useState("");
  const [typingIndicator, setTypingIndicator] = useState(false);

  //mail functionality
  const [loading, setLoading] = useState(false);
  const [activeMember, setActiveMember] = useState({});
  const [activeTab, setActiveTab] = useState(true);
  const [sendable, setSendable] = useState(true);
  const [emailFeature, setEmailFeature] = useState(true);
  const [userDetails, setUserDetails] = useState({});
  const [alreadySent, setAlreadySent] = useState(false);
  const [activeRoomMessage, setActiveRoomMessage] = useState(null);
  const chatBodyRef = useRef(null);
  const manualScrollUp = useRef(false);
  const initals = useRef(getInitals(activeRoom.participant.name));
  const avartStyleRandom = useRef(
    getParticipantColorNumber(activeRoom.participant.id)
  );

  let currentChatListenerRef = useRef(null);
  let typingListenerRef = useRef(null);
  let typingDisconnectRef = useRef(null);

  let inputRef = useRef(null);

  const attachListener = () => {
    let userRef = database.ref(
      `/chat/${AppString.ROOM_MESSAGES}/${activeRoom.roomId}`
    );
    currentChatListenerRef.current = userRef;
    userRef.on(
      "value",
      async (snapshot) => {
        if (snapshot.exists()) {
          let data = snapshot.val();
          setActiveRoomMessage(data);
          removeNotification(user.uid, activeRoom.participant.id);
        }
      },
      (err) => {
        console.error(err);
      }
    );
  };

  useEffect(() => {
    firestore
      .collection("user-invite")
      .doc(`${user.email}+${activeRoom.participant.email}`)
      .get()
      .then((res) => {
        if (res.exists) {
          setAlreadySent(true);
        } else {
          setAlreadySent(false);
        }
      });
    inputRef.current.focus();
    attachListener();
    attachTypingIndicator(
      activeRoom.roomId,
      activeRoom.participant.id,
      typingListenerRef.current,
      (data, err) => {
        if (err) {
          console.error(err);
          if (err.code === "noStatus") {
            setTypingIndicator(false);
          } else {
            console.error(err);
          }
          return;
        }
        setTypingIndicator(data);
      }
    );

    attachOnDisconnectForTyping(
      typingDisconnectRef.current,
      activeRoom.roomId,
      user.uid
    );

    inputRef.current.addEventListener("focusin", () => {
      singalTypingIndicator(activeRoom.roomId, user.uid, true);
    });

    inputRef.current.addEventListener("focusout", () => {
      singalTypingIndicator(activeRoom.roomId, user.uid, false);
    });

    let maxHeight = chatBodyRef.current.getBoundingClientRect().height;
    chatBodyRef.current.addEventListener("scroll", (event) => {
      if (chatBodyRef.current.scrollTop < maxHeight - maxHeight * 0.4) {
        manualScrollUp.current = true;
      } else {
        manualScrollUp.current = false;
      }
    });
    return () => {
      if (currentChatListenerRef.current) currentChatListenerRef.current.off();
      if (typingListenerRef.current) typingListenerRef.current.off();
      if (typingDisconnectRef.current) typingDisconnectRef.current.cancel();
    };
  }, []);

  useEffect(() => {
    if (chatBodyRef.current && !manualScrollUp.current) {
      chatBodyRef.current.scrollTo(0, chatBodyRef.current.scrollHeight);
    }
  }, [activeRoomMessage]);

  const handleBtnClick = (event) => {
    if (event) {
      event.preventDefault();
    }
    let userName = user.displayName
      ? user.displayName
      : user.email.split("@")[0];
    sendMessage1on1(
      userName,
      user.uid,
      activeRoom.participant.id,
      activeRoom.participant.name,
      activeRoom.roomId,
      typedMessage
    );
    chatBodyRef.current.scrollTo(0, chatBodyRef.current.scrollHeight);
    setTypedMessage("");
  };

  const getUserDetails = (allPart, uid) => {
    let details;
    Object.entries(allPart).forEach(([k, v]) => {
      if (v.id == uid) details = v;
      return;
    });
    return details;
  };

  const userDetailsCurr = getUserDetails(allParticipantList, user.uid);

  const sendMail = () => {
    swal({
      title: "Send Invite",
      text: "",
      icon: "info",
      buttons: true,
      dangerMode: true,
    }).then((ok) => {
      if (ok) {
        // this.setState({ loading: true });
        setLoading(true);
        console.log(userDetailsCurr, activeRoom);
        fetch(email_server_endpoint, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            "X-AUTH-TOKEN": "12901cb8edbcd4fff903e48585403839",
          },
          method: "POST",
          body: JSON.stringify({
            sender: {
              ...userDetailsCurr,
            },
            reciver: {
              ...activeRoom.participant,
            },
          }),
        })
          .then(function (res) {
            firestore
              .collection("user-invite")
              .doc(`${user.email}+${activeRoom.participant.email}`)
              .set({
                sender: user.email,
                receiver: activeRoom.participant.email,
                sent: true,
                timestamp: fieldValue.serverTimestamp(),
              })
              .then(() => {
                setSendable(false);
                setLoading(false);
                setAlreadySent(true);
                swal("Sent", {
                  icon: "success",
                });
              });
          })
          .catch(function (res) {
            console.log(res);
          });
      } else {
      }
    });
  };

  return (
    <div className="sidebar__body">
      <div className="chat-section">
        <div
          className="chat-header"
          onClick={closePreviousChatRoom}
          style={{ position: "relative" }}
        >
          <img src={arrowSvg} alt="arrowSvg" />
          <div className="user-profile">
            <span
              className={`user-profile__image ${
                AvatarStyle[avartStyleRandom.current]
              }`}
            >
              {initals.current}
            </span>
            <span className="user-profile__title">
              {activeRoom.participant.name.toLowerCase()}
              {/* <small>
                {typingIndicator
                  ? "typing..."
                  : `${activeRoom.participant.designation}, ${activeRoom.participant.company}`}
              </small> */}
            </span>
          </div>
          {emailFeature ? (
            <RiMailSendLine
              style={{
                position: "absolute",
                right: "2rem",
                pointerEvents: alreadySent ? "none" : "all",
                opacity: alreadySent ? "0.5" : "1",
              }}
              className={`mail ${activeMember ? "" : "d-none"} ${
                activeTab === "tab1" ? "d-none" : ""
              } ${sendable ? "" : "faded"}`}
              size="2rem"
              onClick={(event) => {
                event.stopPropagation();
                sendMail();
              }}
            />
          ) : (
            ""
          )}
        </div>
        <div className="chat-section__body" ref={chatBodyRef}>
          {activeRoomMessage &&
            Object.values(activeRoomMessage).map((messageDetails) => {
              if (messageDetails.id !== user.uid) {
                return (
                  <ReceivedMsg
                    userId={messageDetails.id}
                    time={messageDetails.createdAt}
                    key={messageDetails.createdAt + "+" + messageDetails.id}
                    name={messageDetails.name}
                    message={messageDetails.message}
                  />
                );
              } else {
                return (
                  <SentMsg
                    userId={messageDetails.id}
                    time={messageDetails.createdAt}
                    key={messageDetails.createdAt + "+" + messageDetails.id}
                    name={messageDetails.name}
                    message={messageDetails.message}
                  />
                );
              }
            })}
        </div>
        {activeRoom && (
          <div className="chat-section__footer">
            <div className="chat-section__form">
              <form onSubmit={handleBtnClick}>
                <input
                  type="text"
                  className="chat-section__input"
                  placeholder="Write here"
                  value={typedMessage}
                  onChange={(e) => setTypedMessage(e.target.value)}
                  ref={inputRef}
                ></input>
                <button
                  type="submit"
                  className="chat-section__btn "
                  disabled={typedMessage.length === 0}
                >
                  <i className="icon-send"></i>
                </button>
              </form>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
