import React, { useContext } from 'react'
import { ChatContext } from '../../Context/Chat/ChatContextProvider'
import { MenuStates, UIContext } from '../../Context/UIContextProvider'

export default function Menu() {
    const { activeMenu, setActiveMenu, hidePoll, isPollAdmin } = useContext(UIContext);
    const { publicNotification, setPublicNotification } = useContext(ChatContext)
    return (
        <ul className="sidebar__tabs">
                 <li onClick={() => setActiveMenu(MenuStates.online)}>
                <a
                    className={activeMenu === MenuStates.online ? "active" : ""}
                    href="#"
                >
                    <i className="icon-phone"></i> Online
                </a>
            </li>
            <li onClick={() => setActiveMenu(MenuStates.participants)}>
                <a
                    className={activeMenu === MenuStates.participants ? "active" : ""}
                    href="#"
                >
                    <i className="icon-people"></i> Participants
                </a>
            </li>
       
            <li onClick={() => {
                setPublicNotification({
                    status: false,
                    data: null
                })
                setActiveMenu(MenuStates.publicChat)
            }}>
                <a className={`${activeMenu === MenuStates.publicChat ? "active" : ""}`} href="#"><i className={`${publicNotification.status ? "menu-notification" : ""} icon-chat`}></i> Public Chat</a>
            </li>

            {!hidePoll && (
                <li onClick={() => setActiveMenu(MenuStates.polls)}>
                    <a
                        className={activeMenu === MenuStates.polls ? "active" : ""}
                        href="#"
                    >
                        <i className="icon-polls"></i> Polls
                    </a>
                </li>
            )}
            {
                !hidePoll && isPollAdmin &&
                <li onClick={() => setActiveMenu(MenuStates.notification)}>
                    <a className={activeMenu === MenuStates.notification ? "active" : ""} href="#">
                        <i className="icon-polls"></i> Notification
                    </a>
                </li>
            }
        </ul>
    );
}
