import React, { useContext, useRef, useState } from "react";
import { UserContext } from "../../Context/Auth/UserContextProvider";
import { ChatContext } from "../../Context/Chat/ChatContextProvider";
import { MenuStates, UIContext } from "../../Context/UIContextProvider";
import { AvatarStyle, getInitals } from "../../Utility";
import IncallChat from "../IncallChat/IncallChat";
import "./index.css";
import arrowSvg from "../../Assets/svg/arrow.svg";
import Loader from "../Loader/Loader";
import Fuse from "fuse.js";

const defaultUserRole = "Participant";

const UserTile = ({ user, role, notification }) => {
  const { openChatWithUser, onlineUserslist } = useContext(ChatContext);
  const { getParticipantColorNumber } = useContext(UIContext);

  const initals = useRef(getInitals(user.name));
  const avartStyleRandom = useRef(getParticipantColorNumber(user.id));

  const getDotStyle = () => {
    if (notification) {
      return "user-notification";
    } else if (onlineUserslist.hasOwnProperty(user.id)) {
      return "user-notification user-online-dot";
    } else {
      return "user-notification user-offline-dot";
    }
  };

  return (
    <div
      className="user-profile"
      onClick={() => {
        // console.log(user);
        openChatWithUser(user);
      }}
    >
      <span
        className={`user-profile__image  ${getDotStyle()} ${
          AvatarStyle[avartStyleRandom.current]
        }`}
      >
        {initals.current}
      </span>
      <span className="user-profile__title">
        <b> {user.name.toLowerCase()}</b>
        {/* <small>{`${user.designation}, ${user.company}`}</small> */}
      </span>
      {notification && (
        <span
          className={`notification_count ${
            AvatarStyle[avartStyleRandom.current]
          }`}
        >
          {notification.count}
        </span>
      )}
    </div>
  );
};

const UsersSection = ({ heading, role, userList }) => {
  const { user } = useContext(UserContext);
  const { unseenMessageMetaData } = useContext(ChatContext);
  const [query, setQuery] = useState("");
  let structuredData = [];
  //console.log(userList)
  for (let key in userList)
    structuredData.push({
      id: key,
      name: userList[key].name,
      company: userList[key].company,
      designation: userList[key].designation,
      email: userList[key].email,
    });

  console.log(structuredData);

  const fuse = new Fuse(structuredData, {
    threshold: 0.4,
    includeMatches: false,
    findAllMatches: false,
    includeScore: true,
    keys: ["name", "company", "designation", "email"],
  });

  const results = fuse.search(query);

  console.log("result", results);
  //   const participantResults = query ? results.map(result=>result.item):userList;
  const participantResults = query
    ? results.map((result) => result.item)
    : userList;

  function handleOnSearch({ currentTarget = {} }) {
    const { value } = currentTarget;
    setQuery(value);
  }
  // const participantResults = userList
  //console.log('results',participantResults)
  return (
    <>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <input
          type="text"
          style={{
            width: "100%",
            margin: "2% 0% 0% 2%",
            // height: "5rem",
            border: "none",
            borderRadius: "0.6rem",
            boxShadow: "0px 4px 14px rgb(0 0 0 / 35%)",
            // position:"fixed",
            // top:"0",
            padding: "1rem 1.25rem",
          }}
          value={query}
          onChange={handleOnSearch}
          placeholder="Search Participant"
        />
        {heading && <h2 className="sidebar__title">{heading}</h2>}

        <ul className="sidebar__menu">
          {Object.keys(participantResults).map((userId) => {
            // console.log(userId)
            if (
              participantResults[userId].id !== user.uid &&
              participantResults[userId].name
            ) {
              return (
                <li key={participantResults[userId].id + "--li"}>
                  <UserTile
                    key={participantResults[userId].id}
                    user={participantResults[userId]}
                    role={role}
                    notification={unseenMessageMetaData[userId]}
                  />
                </li>
              );
            } else {
              return null;
            }
          })}
        </ul>
      </div>
    </>
  );
};

function getTrimedMessage(
  sendId,
  senderName,
  currentUserId,
  message,
  limit = 40
) {
  if (!message) {
    return "";
  }
  let result = "";
  if (sendId === currentUserId) {
    result = "me: ";
  } else {
    result = senderName.toLowerCase() + ": ";
  }
  if (message.length > limit) {
    result += message.substr(0, limit) + "...";
  } else {
    result += message;
  }
  return result;
}

const RecentMessageSection = ({ heading, role, recentList }) => {
  const { user } = useContext(UserContext);
  const { unseenMessageMetaData, rawUserData } = useContext(ChatContext);
  return (
    <>
      {heading && <h2 className="sidebar__title">{heading}</h2>}
      <ul className="sidebar__menu">
        {recentList.map((recentMsg) => (
          <>
            {recentMsg.otherParticipant.id && recentMsg.otherParticipant.name && (
              <li key={recentMsg.otherParticipant.id + "recent--li"}>
                <UserTile
                  key={recentMsg.otherParticipant.id + "recent"}
                  user={
                    rawUserData[recentMsg.otherParticipant.id]
                      ? rawUserData[recentMsg.otherParticipant.id]
                      : recentMsg.otherParticipant
                  }
                  role={getTrimedMessage(
                    recentMsg.senderId,
                    recentMsg.senderName,
                    user.uid,
                    recentMsg.message
                  )}
                  notification={
                    unseenMessageMetaData[recentMsg.otherParticipant.id]
                  }
                />
              </li>
            )}
          </>
        ))}
      </ul>
    </>
  );
};

export default function Participants() {
  const {
    onlineUserslist,
    userLastInteractedRoom,
    allParticipantList,
    incallChatActive,
    showInCallChat,
    setShowInCallChat,
  } = useContext(ChatContext);
  const { activeMenu } = useContext(UIContext);
  console.log(activeMenu);
  return (
    <>
      {showInCallChat && incallChatActive.status && incallChatActive.roomId && (
        <IncallChat />
      )}
      {!showInCallChat && (
        <div
          className="sidebar__body"
          style={{
            padding: "0 1rem",
            overflow: "auto",
            height: "90%",
            marginTop: "6em",
          }}
        >
          {incallChatActive.status && incallChatActive.roomId && (
            <div
              className="chat-header"
              onClick={() => setShowInCallChat(true)}
            >
              <div className="user-profile">
                <span className="user-profile__title">In Call Messages</span>
              </div>
              <img className="enterArrow" src={arrowSvg} alt="arrowSvg" />
            </div>
          )}
          {userLastInteractedRoom && activeMenu === MenuStates.online && (
            <RecentMessageSection
              heading={`Recent (${userLastInteractedRoom.length})`}
              role="Participant"
              recentList={userLastInteractedRoom}
            />
          )}
          {
            activeMenu === MenuStates.online &&
            Object.keys(onlineUserslist).length > 1 ? (
              <UsersSection
                heading={
                  onlineUserslist
                    ? `Online  (${Object.keys(onlineUserslist).length - 1})`
                    : ""
                }
                role="Participant"
                userList={onlineUserslist}
              />
            ) : // <UsersSection heading={onlineUserslist ? "In Call" : ''} role="Participant" userList={onlineUserslist} />
            null
            //<Loader />
          }
          {activeMenu === MenuStates.participants &&
            Object.keys(allParticipantList).length > 0 && (
              <UsersSection
                heading={
                  // allParticipantList
                  //   ? `All Participants (${Object.keys(allParticipantList).length - 1
                  //   })`
                  //   : ""
                  ""
                }
                role="Participant"
                userList={allParticipantList}
              />
            )}
        </div>
      )}
    </>
  );
}
