import React, { createContext, useState, useEffect } from "react";
import { auth, login, updateName } from "../../Firebase";
import {
  getUserDetails,
  UpdateUserTable,
  user_ActiveStatus,
} from "../../Firebase/chatManager";

export const UserContext = createContext();

export default function UserContextProvider(props) {
  const [user, setUser] = useState(
    JSON.parse(localStorage.getItem("userAuth"))
  );

  useEffect(() => {
    window.parent.loginUser = (email, password) => {
      login(email, password);
    };
    auth.onAuthStateChanged(async (user) => {
      if (user) {
        console.log(user.email, user.displayName);
        localStorage.setItem("userAuth", JSON.stringify(user));
        const userOtherData = await getUserDetails(user);
        await UpdateUserTable({ ...user, ...userOtherData });
        user_ActiveStatus({ ...user, ...userOtherData });
        setUser({ ...user, isChecked: true });
        // updateName(user,"Siddharth Vyas")
      } else {
        localStorage.removeItem("userAuth");
        setUser(null);
        // login("shubham@dj.com", "up14y7434")
        // login("testuser1@chat.com","up14y7434")
        // login("testuser2@chat.com","up14y7434")
      }
    });
  }, []);

  return (
    <UserContext.Provider value={{ user }}>
      {props.children}
    </UserContext.Provider>
  );
}
