import { createContext, useContext, useEffect, useRef, useState } from "react";
import { firestore } from "../Firebase";
import { UserContext } from "./Auth/UserContextProvider";

export const UIContext = createContext();

export const MenuStates = {
    participants: 0,
    publicChat: 1,
    polls: 2,
    notification: 3,
    online:4
};

export const UIContextProvider = (props) => {
    const { user } = useContext(UserContext)
    const [activeMenu, setActiveMenu] = useState(MenuStates.participants);
    const [hidePoll, setHidePoll] = useState(true)
    const [isPollAdmin, setPollAdmin] = useState(false)
    const userColorClassRecord = useRef(localStorage.getItem("participantColor") ? JSON.parse(localStorage.getItem("participantColor")) : {})


    useEffect(() => {
        const urlQuery = new URLSearchParams(window.location.search);
        let showPoll = urlQuery.get("showPoll");
        if (showPoll === "true") {
            setHidePoll(false)
        }

        // let publicRoomURl = urlQuery.get("publicRoom");
        // if (publicRoomURl) {
        //     setPublicRoom(publicRoomURl)
        // }

        let pollAdmin = urlQuery.get("pollAdmin");
        if (pollAdmin === "true") {
            setPollAdmin(true)
        } else {
            // setPollAdmin(false)
        }

        // let videoCall = urlQuery.get("videoCall");
        // if (videoCall === "true") {
        //     setHidePoll(false)
        // } else {

        // }

    }, [])


    const getParticipantColorNumber = (id) => {
        // console.log(id)
        let mainObject = userColorClassRecord.current
        if (!mainObject.hasOwnProperty(id)) {
            let val = Math.floor(Math.random() * 3)
            mainObject[id] = val
            localStorage.setItem("participantColor", JSON.stringify(userColorClassRecord.current))
        }
        return mainObject[id]

    }


    return (
        <UIContext.Provider value={{ activeMenu, setActiveMenu, hidePoll, isPollAdmin, getParticipantColorNumber }}>
            {props.children}
        </UIContext.Provider>
    )
}
