export function login(name, password) {
  auth
    .signInWithEmailAndPassword(name, password)
    .then((userCred) => {
      console.log(userCred);
    })
    .catch((err) => console.log(err))
    .finally((a) => console.log(a, "finaly"));
}

export function updateName(user, name) {
  if (!user.displayName) {
    user.updateProfile({
      displayName: name,
    });
  }
}
